import React, { useEffect, useState } from "react";
import { useLocation, useParams, Link, useNavigate } from "react-router-dom";
import Papa from "papaparse";

export default function Individual() {
  const { id } = useParams();
  const location = useLocation();
  const navigate = useNavigate();

  const [article, setArticle] = useState(null);

  useEffect(() => {
    const sheetUrl =
      "https://docs.google.com/spreadsheets/d/e/2PACX-1vQeomN__blTC2EylL4uEwGxLXMh0zxQ0F5BlWwBNHySrtJwqw9aJe9Zw0166-pDhaboaM4GdTlhzAET/pub?gid=0&single=true&output=csv";

    Papa.parse(sheetUrl, {
      download: true,
      header: true,
      complete: (result) => {
        const cleaned = result.data.filter(
          (row) => row.id && row.title
        );

        const found = cleaned.find(
          (a) => String(a.id) === String(id)
        );

        setArticle(found || null);
      },
    });
  }, [id]);

  if (!article) {
    return (
      <div className="w-full min-h-screen flex flex-col items-center justify-center p-6 text-center text-white bg-gradient-to-r from-[#9B2A22] via-[#4B0F0C] to-[#2F0D0A]">
        <p className="text-lg sm:text-xl md:text-2xl">Loading article...</p>
        <div className="mt-4">
          <Link
            to="/"
            className="text-yellow-300 underline hover:text-yellow-400 transition"
          >
            Back to home
          </Link>
        </div>
      </div>
    );
  }

  return (
    <div className="w-full min-h-screen bg-gradient-to-r from-[#9B2A22] via-[#4B0F0C] to-[#2F0D0A] text-white px-4 sm:px-6 md:px-10 lg:px-20 xl:px-32 py-10 font-display">
      <div className="max-w-5xl mx-auto bg-[#EBEAEA] text-black rounded-2xl shadow-xl p-6 sm:p-8 md:p-10">

        {/* TITLE */}
        <h1 className="text-3xl sm:text-4xl md:text-5xl text-center font-bold text-[#8B0D0D] mb-6">
          {article.title}
        </h1>

        {/* AUTHOR + TIME */}
        <div className="text-center mb-6">
          <p className="text-[#6D1111] text-lg sm:text-xl font-semibold">
            {article.author}
          </p>
          <p className="text-gray-600 text-sm">{article.time}</p>
        </div>

        {/* DESCRIPTION */}
        <p className="text-sm sm:text-base md:text-lg leading-relaxed whitespace-pre-line text-gray-800">
          {article.description}
        </p>

        {/* BACK BUTTON */}
        <div className="flex justify-center mt-10">
          <button
            onClick={() => navigate(-1)}
            className="bg-[#8B0D0D] text-white py-2 px-6 sm:px-8 rounded-lg hover:bg-[#6D1111] transition duration-300 text-sm sm:text-base"
          >
             Back
          </button>
        </div>
      </div>
    </div>
  );
}
